.banner {
    background-color: black;
    width: 100%;
    height: 448px;
    background-size: cover;
    color: white;
}
.content{
    padding-top: 140px;
    height:190px;
    padding-left:15px;

}
.title{
    font-size: 3rem;
    font-weight: 0.3rem;
}
.button{
 color: white;
 outline: none;
 border: none;
 font-weight: 700;
 border-radius: 5px;
 padding-left:2rem;
 padding-right: 2rem;
 padding-top: 0.5rem;
 padding-bottom:0.5rem;
 background-color: rgba(51,51,51,0.5);
 cursor: ponter;
 margin-right:1rem;
}
.button:hover{
    color:black;
    background-color:#e6e6e6;
}
.description{
    width:45rem;
    line-height:1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 8px;
    max-width: 360px;
}
.fade_bottom{
    height: 7.4rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,0.61),#111);
}